import React from 'react';
import { HashRouter as Router, Route } from 'react-router-dom';

import MyComponentWithFilters from './examples/with-filters';
import MyModal from './examples/modal';

import '@wordpress/components/build-style/style.css';

const App = () => (
  <Router>
    <div>
      <Route path={`${process.env.PUBLIC_URL}/modal`} component={MyModal} />
      <Route path={`${process.env.PUBLIC_URL}/with-filters`} component={MyComponentWithFilters} />
    </div>
  </Router>
);

export default App;
